body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    width: 100%;
    height: 300px;
    background-image: url(/clickygame3/static/media/nickback2.949be400.jpg);
    background-size: 100px 175px;
    border-bottom: 8px solid purple;
  }
  .tophead {
    width: 100%;
    height: 100px;
    background-color: purple;
    margin: 0;
    position: fixed;
    top: 0;
    text-align: center;
  }
  .scores {
    font-size: 20px;
    font-weight: bolder;
    text-align: center;
  }
  .resulttxt {
    font-size: 25px;
  }
  
  .bottomhead {
    text-align: center;
  }
  
  .btitle {
    font-weight: bolder;
    font-size: 40px;
    color: purple;
    margin-top: 100px;
  }
  
  .binstruct {
    font-weight: bolder;
    font-size: 30px;
    color: purple;
  }
  #s {
    padding-right: 30px;
    border-right: 2px solid white;
    margin-right: 20px;
  }
 
  @media only screen and (min-width: 600px) {
   
  }
.nickpics {
  width: 100px;
  height: 100px;
  border: 1px solid purple;
  float: left;
  margin: 1px;
}


@media only screen and (min-width: 600px) {
  .nickpics {
    width: 150px;
    height: 150px;
    border: 4px solid purple;
    margin: 10px;
  }
}

.wrapper {
  margin: 20px auto;
  width: 90%;
  height: 80vh;
}

@media only screen and (min-width: 600px) {
  .wrapper {
    width: 750px;
    height: 560px;
  } 
}
